nav.menu {
    display: flex;
    align-items: center;
}

nav.menu ul {
    display: flex;
    gap: var(--lg-spacing);
    margin: 0;
    padding: 0;
    font-weight: 100;
    color: var(--gray-1);
}

nav.menu li {
    list-style: none;
    font-family: 'Merriweather Sans', sans-serif;
    text-transform: uppercase;
}

/* Animación linea */

.menu-item {
    position: relative;
    cursor: pointer;
    padding: 5px 0;
}

.menu-item a{
    color: inherit;
    text-decoration: none;
    font-weight: 300;
}

.menu-item:hover, .menu-item.active {
    color: black;
    font-weight: 400;
    transition: 0.15s ease-in-out;
}

.menu-item.active::after {
    content: "";
    position: absolute;
    left: 50%;
    /* Empieza en el centro */
    bottom: 0;
    /* Pegado abajo del texto */
    width: 100%;
    height: 2px;
    background-color: var(--primary-color);
}

.menu-item::after {
    content: "";
    position: absolute;
    left: 50%;
    /* Empieza en el centro */
    bottom: 0;
    /* Pegado abajo del texto */
    width: 0;
    height: 2px;
    background-color: var(--primary-color);
    transition: all 0.15s ease;
    transform: translateX(-50%);
    /* Centra el punto inicial */
}

/* Al hacer hover */
.menu-item:hover::after {
    width: 100%;
    /* Se expande de centro a bordes */
}

.menu-hamburger {
    background: none;
    border: none;
    cursor: pointer;
    margin-left: auto;
    display: flex;
    align-items: center;
    padding: 8px;
    color: var(--gray-1);
    transition: color 0.2s;
    display: none;;
}

.menu-hamburger img {
    width: 24px;
    height: 24px;
}

.menu-hamburger:hover {
    color: var(--primary-color);
}

@media screen and (max-width: 768px) {
    nav.menu ul {
        display: none; /* Oculta el menú en pantallas pequeñas */
    }

    .menu-hamburger {
        display: flex; /* Muestra el botón de menú en pantallas pequeñas */
    }
    
}