.hero {
    display: flex;
    justify-content: center;
    gap: var(--lg-spacing);
    min-height: 75vh;
    height: 100%;
    align-content: center;
    flex-wrap: wrap;
    font-family: 'Merriweather Sans', sans-serif;
    border-bottom: 1px solid var(--gray-3);
    position: relative;
}

.hero img.profile-picture {
    width: 250px;
    height: 250px;
}

.hero div {
    max-width: 35vw;
}

.hero h2 {
    font-size: 3em;
    font-weight: 900;
    font-family: 'Nexa','Merriweather Sans', sans-serif;
    margin-top: 0;
        justify-content: flex-start;
}

.hero h2 span {
    color: var(--primary-color);
}

.hero p {
    font-size: 1.5em;
    font-weight: 100;
    line-height: 2em;
    color: var(--gray-2);
    font-family: 'Merriweather';
    letter-spacing: 0.02em;
}

.hero p span {
    color: var(--accent-color);
    font-weight: 600;
}

.hero .cta {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: var(--smp-spacing);
    padding: var(--smp-spacing) var(--mdp-spacing);
    position: relative;
    background-color: #ffffff;
    border-radius: var(--lg-spacing);
    border: 1px solid;
    border-color: var(--gray-3);
    position: absolute;
    bottom: -26px;
}

.hero .cta img {
    width: var(--mdp-spacing);
}

.hero .cta p {
    font-family: "Merriweather Sans-Light", Helvetica;
    font-weight: 300;
    color: var(--gray-2);
    font-size: 14px;
    letter-spacing: 2.52px;
    text-transform: uppercase;
    margin: 0;
}